.modal-wrapper {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  z-index: 90;
}

.active-modal-wrap {
  display: block;
}

.modal-window {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  max-width: 1000px;
  min-height: 600px;
  padding: 1rem 0 1rem 1rem;
  background-color: #fff;
  border-radius: 20px;
  z-index: 100;
  overflow-y: auto;
}

.modal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  height: 600px;
  padding: 1rem 1rem 1rem 0;
  overflow-y: auto;

  &__info-col {
    flex: 1;
    padding: 0 0.5rem;
  }

  &__info {
    padding: 1rem 0;
  }

  &__img {
    display: block;
    width: 60%;
    height: auto;
    object-fit: contain;
    float: left;
    padding: 0 1rem 0.18rem 0;
  }

  &__title {
    margin-bottom: 1rem;
    font-weight: bold;
  }

  &__btn-box {
    clear: both;
    padding: 1rem 0;

    &--wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 1rem;
    }
  }

  &__btn {
    display: block;
    width: 200px;
    min-height: 54px;
    border-radius: 1rem;
    border: 2px solid #ccc;
    padding: 0.5rem 0;
    font-size: 0.8rem;
    text-align: center;
    color: #000;
    background-color: lightgray;
    transition: box-shadow 0.3s;

    &--favorite {
      background-color: orangered;
    }

    &:hover {
      box-shadow: 0 0 10px rgba(0,0,0,0.5);
    }
  }
}

.react-router-modal {
  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
    overflow-y: auto;
  }

  &__modal {
    width: 90%;
    max-width: 1024px;
    margin: 0 auto;
    background-color: #fff;
    border-radius: 0.5rem;
  }
}

.load-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  &__spinner {
    font-weight: 800;
    text-align: center;
  }
}
